<template>
    <div class="dt-calculator">
        <div class="dt-calculator__value">
            <span>Сумма займа</span>
            <input class="dt-calculator__value-input-desktop" type="number" v-model.number="calcSum"
                   @focus="clearField()"
                   @blur="isFieldEmpty()"
                   @change="checkInputDragData()">

            <input class="dt-calculator__value-input-mobile" type="tel" v-model.number="calcSum"
                   @focus="clearField()"
                   @blur="isFieldEmpty()"
                   @change="checkInputDragData()">
            <div class="dt-calculator__value-text">грн</div>
        </div>
        <div class="dt-calculator__slider">
            <div class="dt-calculator__slider-minus" @click="toIncremetDragValue(false, 100)"></div>
            <div class="dt-calculator__slider-wrap">
                <div @click="changeRange(minValue)" class="dt-calculator__slider-before"></div>
                <vue-slider @change="changeRange($event)" @dragging="changeRange($event)" v-model="rangeValue" v-bind="options"></vue-slider>
                <div @click="changeRange(maxValue)" class="dt-calculator__slider-after"></div>
            </div>
            <div class="dt-calculator__slider-plus" @click="toIncremetDragValue(true, 100)"></div>
        </div>
        <div class="dt-calculator__period">
            <span>Срок займа</span>
            <div class="dt-calculator__term-container">
                <div v-for="(loanTerm, i) in [7,14,21,30]" :key="i"
                     :class="`dt-calculator__term-button ${period == loanTerm ? 'dt-calculator__active-term-button' : ''}`"
                     v-on:click="changeDate(loanTerm)">
                    <span>{{ loanTerm }}</span>
                    <span>{{loanTerm === 21 ? 'день' : 'дней'}}</span>
                </div>
            </div>
        </div>
        <div class="dt-calculator__phone-block">
            <phone-input :name="'phone'"
                         v-validate="{required: true, regex: /^\+380\s\([0-9]{2}\)\s[0-9]{3}-[0-9]{2}-[0-9]{2}$/}"
                         v-model="phone"
                         :placeholder="'+380 (##) ###-##-##'"
                         @input="changeInput($event)"
                         :class="{'validate-error': errors.has('phone')}">
            </phone-input>
        </div>
        <div class="dt-calculator__info">
            <div class="dt-calculator__info-credit-sum">
                <span>Сумма займа</span>
                <span></span>
            </div>
            <div class="dt-calculator__info-percent">
                <span>Проценты</span>
                <span>{{percentSum}} грн</span>
            </div>
            <div class="dt-calculator__info-full-sum">
                <span>Всего к оплате</span>
                <span>{{allSum}} грн</span>
            </div>
            <div class="dt-calculator__info-end-date">
                <span>Дата оплаты</span>
                <span>{{endDate}}</span>
            </div>
        </div>
        <div class="dt-button-block">
            <div @click="submitData" :class="this.isPhoneNumberValid ? 'dt-button' : 'dt-disabled-button'">Подать заявку</div>
        </div>
    </div>
</template>

<script>
    import PhoneInput from '../Inputs/PhoneInput';
    import VueSlider from 'vue-slider-component';
    import './range-slider.css';


    export default {
        data() {
            return {
                calcSum: 1300,
                period: 30,
                currentValue: 100,
                allSum: 0,
                daySum: 0,
                interest: 1.5,
                minValue: 500,
                maxValue: 15000,
                rangeValue: 500,
                tempCalcValue: 0,
                endDate: '',
                phone: '+380 (',
                percentSum: '',
                isPhoneNumberValid: false,
                options: {
                    dotSize: 44,
                    width: 'calc(100% - 44px)',
                    height: 6,
                    contained: true,
                    direction: 'ltr',
                    data: null,
                    min: 500,
                    max: 15000,
                    interval: 100,
                    disabled: false,
                    clickable: true,
                    duration: 0.2,
                    adsorb: false,
                    lazy: false,
                    tooltip: 'none',
                    useKeyboard: false,
                }
            };
        },
        components: {
            PhoneInput,
            VueSlider,
        },
        methods: {
            async submitData() {
                const isValidePhoneNumber = await this.$validator.validateAll();
                if (!isValidePhoneNumber) return;
                const calculatorData = {
                    calculatorSum: this.calcSum,
                    calculatorPeriod: this.period,
                    phoneNumber: this.phone,
                };
                this.$emit('readyToSubmit', calculatorData);
            },
            changeInput(value) {
                if (/^\+380\s\([0-9]{2}\)\s[0-9]{3}-[0-9]{2}-[0-9]{2}$/.test(value)) {
                    this.isPhoneNumberValid = true;
                } else this.isPhoneNumberValid = false;
            },
            isFieldEmpty() {
                if (this.calcSum === '') {
                    this.calcSum = this.tempCalcValue;
                }
            },
            clearField() {
                this.tempCalcValue = this.calcSum;
                this.calcSum = '';
            },
            checkInputDragData() {
                if (this.calcSum > this.maxValue) {
                    this.calcSum = this.maxValue;
                }

                if (this.calcSum < this.minValue) {
                    this.calcSum = this.minValue;
                }

                this.calcSum = Math.floor(this.calcSum / 100) * 100;
                this.changeRange(this.calcSum);
            },
            toIncremetDragValue(operator, step) {
                if (operator) {
                    if (this.calcSum === this.maxValue) return;
                    this.changeRange(this.calcSum + step);
                } else {
                    if (this.calcSum === this.minValue) return;
                    this.changeRange(this.calcSum - step);
                }
            },
            changeRange(val) {
                this.calcSum = val;
                this.rangeValue = val;
                this.setData();
                this.$el.querySelector('.dt-calculator__info-credit-sum span:last-child').innerText = `${this.calcSum} грн`;
            },
            setData() {
                this.toEndDate();

                this.allSum = (this.calcSum / 100 * this.interest * this.period + this.calcSum).toFixed(2);
                this.daySum = this.calcSum / 100 * this.interest;

                this.percentSum = Math.ceil(this.daySum * this.period);

                this.allSum = Math.ceil(this.allSum);
                this.daySum = Math.ceil(this.daySum);
            },
            toEndDate() {
                const date = new Date();

                date.setDate(date.getDate() + parseInt(this.period, 10));

                let day = date.getDate();

                if (day < 10) {
                    day = `0${day}`;
                }

                let month = date.getMonth() + 1;
                if (month < 10) {
                    month = `0${month}`;
                }
                const year = date.getFullYear();

                this.endDate = `${day}.${month}.${year}(${this.getWeekDay(date)})`;
            },
            getWeekDay(date) {
                const days = ['вс', 'пн', 'вт', 'ср', 'чт', 'пт', 'сб'];
                return days[date.getDay()];
            },
            changeDate(date) {
                this.period = date;
                this.setData();
            },

        },
        mounted() {
            this.changeRange(5000);
        },

    };
</script>