<template>
    <div class="dt-input-block">
        <p class="dt-field-label">{{ this.label }}</p>
        <the-mask type="tel" :name="this.name"
                  :placeholder="this.placeholder"
                  @input="update"
                  :mask="['#####']"></the-mask>
        <slot></slot>
    </div>
</template>

<script>
import {TheMask} from 'vue-the-mask';

export default {
    components: {
        TheMask,
    },
    props: {
        placeholder: {
            type: String,
            default: '-- -- --',
        },
        label: {
            type: String,
            required: false,
        },
        name: {
            type: String,
            required: true,
        },
        value: {
        },
    },
    methods: {
        update(value) {
            this.$emit('input', value);
        },
    },
};
</script>
