<template>
    <div class="dt-verify">
        <modal :message="modalMessage"
               :face="'Продолжить'"
                v-if="showModal"
                @close="closeModal">
        </modal>

        <div class="dt-default-popup" v-if="showPopup">
            <div class="dt-default-popup__outer">
                <div @click.stop="showPopup = false" class="dt-default-popup__overlay"></div>
                <div class="dt-default-popup__block">
                    <div @click="showPopup = false" class="dt-default-popup__close"></div>
                    <phone-input :name="'newPhone'"
                                 v-validate="{required: true, regex: /^\+380\s\([0-9]{2}\)\s[0-9]{3}-[0-9]{2}-[0-9]{2}$/}"
                                 v-model="newPhone"
                                 @input="getNewPhone($event)"
                                 :data-vv-as="$t('phone')"
                                 :placeholder="'+380 (##) ###-##-##'"
                                 :label="'Введите новый номер телефона'"
                                 :class="{'validate-error': errors.has('newPhone')}">

                        <validation-error v-if="errors.has('newPhone')"
                                          :message="errors.first('newPhone')">
                        </validation-error>
                    </phone-input>
                    <div class="dt-button-block">
                        <div @click="submitPhoneNumber" :class="this.isNewPhoneFill ? 'dt-button' : 'dt-disabled-button'">Сменить номер</div>
                        <span @click="showPopup = false" class="dt-link">Отменить</span>
                    </div>
                </div>
            </div>
        </div>

        <div class="dt-block-title">Регистрация</div>
        <div class="dt-verify__info-block">
            <span>Номер телефона</span>
            <span>{{this.phone}}</span>
        </div>
        <div class="dt-verify__info-block">
            <span>E-mail</span>
            <span>{{this.email}}</span>
        </div>

        <sms-code :name="'telephone_token'"
                  :data-vv-as="$t('telephone_token')"
                  v-model="telephoneToken"
                  :placeholder="'- - - - -'"
                  v-validate="'required|min:5|max:5'"
                  @input="getCode($event)"
                  :class="{'validate-error': errors.has('telephone_token')}"
                  :label="'Введите пароль из СМС'">

            <validation-error v-if="errors.has('telephone_token')"
                              :message="errors.first('telephone_token')"></validation-error>
        </sms-code>

        <div class="dt-button-block">
            <div @click="onSubmit" :class="this.isCodeFill ? 'dt-button' : 'dt-disabled-button'">Продолжить</div>
            <div @click="sendSmsAgain" class="dt-white-button">Отправить СМС еще раз</div>
            <span @click="changePhoneNumber" class="dt-link">Сменить номер телефона</span>
        </div>
    </div>
</template>

<script>
    import ValidationError from '../Inputs/ValidationError';
    import SmsCode from'../Inputs/SmsCode';
    import PhoneInput from '../Inputs/PhoneInput';
    import Modal from '../Modal/Modal';

    export default {
        props: {
            phone: {
                type: String,
                required: true,
            },
            email: {
                type: String,
                required: true,
            },
        },
        data() {
            return {
                telephoneToken: '',
                showPopup: false,
                newPhone: '+380 (',
                modalMessage: '',
                showModal: false,
                isCodeFill: false,
                isNewPhoneFill: false,
            };
        },
        components: {
            ValidationError,
            SmsCode,
            PhoneInput,
            Modal,
        },
        methods: {
            getNewPhone(newPhoneValue) {
                newPhoneValue.length === 19 ? this.isNewPhoneFill = true : this.isNewPhoneFill = false;
            },
            getCode(codeValue) {
                codeValue.length === 5 ? this.isCodeFill = true : this.isCodeFill = false;
            },
            closeModal() {
                this.showModal = false;
            },
            async onSubmit() {
                if (!await this.$validator.validateAll()) return;
                alert('Code sent!!!');
            },
            sendSmsAgain() {
                this.modalMessage = 'Hello Dima! sdf sdfsdf sdfsd sdfsd sdfs sdfsd ';
                this.showModal = true;
            },
            changePhoneNumber() {
                this.showPopup = true;
            },
            async submitPhoneNumber() {
                if (!await this.$validator.validate('newPhone')) return;
                alert('New phone number sent!!!')
            },
        },
    };
</script>