<template>
    <div class="dt-input-block">
        <p v-if="this.label" class="dt-field-label">{{ this.label }}<span v-if="this.notation" class="dt-notation-content" title="Обязательное поле!">*</span></p>
        <the-mask :autocomplete="'off'"
                  :type="'tel'"
                  :masked="true"
                  :name="this.name"
                  :placeholder="this.placeholder"
                  :value="value"
                  @input="update"
                  @change.native="createChangeEvent"
                  :mask="['+380 (##) ###-##-##']">
        </the-mask>
        <slot></slot>
    </div>
</template>

<script>
import {TheMask} from 'vue-the-mask';

export default {
    components: {
        TheMask,
    },
    props: {
        placeholder: {
            type: String,
            default: '',
        },
        notation: {
            type: Boolean,
            required: false,
            default: false,
        },
        label: {
            type: String,
            required: false,
        },
        name: {
            type: String,
            required: true,
        },
        value: {
        },
    },
    methods: {
        update(value) {
            this.$emit('input', value);
        },
        createChangeEvent() {
            this.$emit('change');
        },
    },
};
</script>
