<template>
    <transition name="dt-modal-fade" @click="close">
        <div class="dt-modal-container" @click="close">
            <div @click.stop class="dt-modal" role="dialog" aria-labelledby="modalTitle" aria-describedby="modalDescription">
              <div class="dt-default-popup__close" @click="close"></div>
              <section class="dt-modal__body" id="dt-modalDescription">
                    <slot name="body">
                      <div class="dt-modal-text" v-html="message"></div>
                    </slot>
              </section>
              <div class="dt-button-block">
                    <div @click="close" class="dt-button">{{this.face}}</div>
              </div>
            </div>
        </div>
    </transition>
</template>

<script>
    export default {
        name: 'modal',
        props: {
            message: String,
            title: String,
            face: String,
        },
        methods: {
            close() {
                this.$emit('close');
            },
        },

        mounted() {
            document.addEventListener('keydown', (e) => {
                if (e.keyCode === 27) {
                    this.close();
                }
            });
        },
    };
</script>
