<template>
    <div class="dt-input-block">
        <p v-if="this.label" class="dt-field-label">{{ this.label }}<span v-if="this.notation" class="dt-notation-content" title="Обязательное поле!">*</span></p>
        <input type="password" :name="this.name"
               maxlength="20"
               :value="this.value"
               :placeholder="placeholder"
               @keypress="getCharCode($event)"
               @input="update">
        <slot></slot>
        <div v-if="showLanguagePanel" class="dt-password-language">{{passLanguage}}</div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            passLanguage: '',
            showLanguagePanel: false,
        };
    },
    props: {
        placeholder: {
            type: String,
            default: '',
        },
        notation: {
            type: Boolean,
            required: false,
            default: false,
        },
        label: {
            type: String,
            required: false,
        },
        name: {
            type: String,
            required: true,
        },
        value: {
        },
    },
    methods: {
        getCharCode(e) {
            if (/[a-zA-Z]/.test(String.fromCharCode(e.charCode))) {
                this.passLanguage = 'EN';
                this.showLanguagePanel = true;
            } else if (/[а-яїєіёА-ЯЇЄІЁ]/.test(String.fromCharCode(e.charCode))) {
                this.passLanguage = 'UA/RU';
                this.showLanguagePanel = true;
            }
        },
        update(value) {
            this.$emit('input', value.target.value);
            if (!value.target.value) {
                this.passLanguage = '';
                this.showLanguagePanel = false;
            }
        },
    },
};
</script>
