<template>
    <div class="dt-input-block">
        <p v-if="label" class="dt-field-label">{{ this.label }}<span v-if="this.notation" class="dt-notation-content" title="Обязательное поле!">*</span></p>
        <the-mask v-if="this.mask" :type="this.type"
                  :name="this.name"
                  :masked="this.masked"
                  :mask="this.mask"
                  :value="this.value"
                  :placeholder="placeholder"
                  @change.native="createChangeEvent"
                  @input="update">
        </the-mask>
        <input autocomplete="off"
               :type="this.type"
               v-if="!this.mask"
               :disabled="this.disabled"
               :name="this.name"
               :value="this.value"
               :placeholder="this.placeholder"
               @input="inputUpdate"
               @change="createChangeEvent"
               :maxlength="this.maxlength"/>
        <slot></slot>
    </div>
</template>

<script>
import {TheMask} from 'vue-the-mask';

export default {
    components: {TheMask},
    props: {
        type: {
            type: String,
            default: 'text',
        },
        disabled: {
            type: Boolean,
            required: false,
        },
        maxlength: {
            type: String,
            required: false,
        },
        mask: {
            type: String,
            default: '',
        },
        masked: {
            type: Boolean,
            default: false,
        },
        placeholder: {
            type: String,
            default: '',
        },
        notation: {
            type: Boolean,
            required: false,
            default: false,
        },
        label: {
            type: String,
            required: false,
        },
        name: {
            type: String,
            required: true,
        },
        value: {
        },
    },
    data() {
        return {
            defaultTokens: {
                C: {
                    pattern: /[а-яА-Я]/,
                    transform: v => v.toLocaleUpperCase(),
                },
            },
        };
    },
    methods: {
        update(value) {
            this.$emit('input', value);
        },
        inputUpdate(value) {
            this.$emit('input', value.target.value);
        },
        createChangeEvent() {
            this.$emit('change');
        },
    },
};
</script>
