<template>
    <div class="dt-registration">
        <div class="dt-block-title">Регистрация</div>
        <phone-input :name="'regPhone'"
                     v-validate="{required: true, regex: /^\+380\s\([0-9]{2}\)\s[0-9]{3}-[0-9]{2}-[0-9]{2}$/}"
                     v-model="regPhone"
                     :data-vv-as="$t('phone')"
                     :placeholder="'+380 (##) ###-##-##'"
                     :class="{'validate-error': errors.has('regPhone')}">

            <validation-error v-if="errors.has('regPhone')"
                              :message="errors.first('regPhone')">
            </validation-error>
        </phone-input>

        <text-input :name="'email'"
                    v-validate="'required|email'"
                    :data-vv-as="$t('email')"
                    :class="{'validate-error': errors.has('email')}"
                    :placeholder="'E-mail'"
                    v-model="email">

            <validation-error v-if="errors.has('email')"
                              :message="errors.first('email')"></validation-error>
        </text-input>

        <password-input v-on:input="changeInput"
                        :name="'password'"
                        :data-vv-as="$t('password')"
                        :placeholder="'Пароль'"
                        ref="password"
                        v-validate="{required:true, regex: /(\d.*[A-ZА-ЯЇЄІЁ].*[a-zа-яїєіё])|(\d.*[a-zа-яїєіё].*[A-ZА-ЯЇЄІЁ])|([A-ZА-ЯЇЄІЁ].*\d.*[a-zа-яїєіё])|([A-ZА-ЯЇЄІЁ].*[a-zа-яїєіё].*\d)|([a-zа-яїєіё].*\d.*[A-ZА-ЯЇЄІЁ])|([a-zа-яїєіё].*[A-ZА-ЯЇЄІЁ].*\d)/, min: 6, max: 20}"
                        :class="{'validate-error': errors.has('password')}"
                        v-model="password">

            <validation-error v-if="errors.has('password')"
                              :message="errors.first('password')"></validation-error>
        </password-input>

        <div class="dt-verification-password-checkboxes">
            <div>
              <span :class="{'dt-verification-password-checkboxes__ready':hasSixChars}">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"
                     width="12px" height="12px" style="padding-bottom: -2px">
                  <path fill="currentColor"
                        d="M173.898 439.404l-166.4-166.4c-9.997-9.997-9.997-26.206 0-36.204l36.203-36.204c9.997-9.998 26.207-9.998 36.204 0L192 312.69 432.095 72.596c9.997-9.997 26.207-9.997 36.204 0l36.203 36.204c9.997 9.997 9.997 26.206 0 36.204l-294.4 294.401c-9.998 9.997-26.207 9.997-36.204-.001z">
                  </path>
                </svg>
              минимум 6 символов</span>
                <span :class="{'dt-verification-password-checkboxes__ready':hasLower}">
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"
                         width="12px" height="12px" style="padding-bottom: -2px">
                      <path fill="currentColor"
                            d="M173.898 439.404l-166.4-166.4c-9.997-9.997-9.997-26.206 0-36.204l36.203-36.204c9.997-9.998 26.207-9.998 36.204 0L192 312.69 432.095 72.596c9.997-9.997 26.207-9.997 36.204 0l36.203 36.204c9.997 9.997 9.997 26.206 0 36.204l-294.4 294.401c-9.998 9.997-26.207 9.997-36.204-.001z">
                      </path>
                    </svg>
                    маленькие буквы</span>
            </div>
            <div>
              <span :class="{'dt-verification-password-checkboxes__ready':hasUpper}">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"
                     width="12px" height="12px" style="padding-bottom: -2px">
                  <path fill="currentColor"
                        d="M173.898 439.404l-166.4-166.4c-9.997-9.997-9.997-26.206 0-36.204l36.203-36.204c9.997-9.998 26.207-9.998 36.204 0L192 312.69 432.095 72.596c9.997-9.997 26.207-9.997 36.204 0l36.203 36.204c9.997 9.997 9.997 26.206 0 36.204l-294.4 294.401c-9.998 9.997-26.207 9.997-36.204-.001z">
                  </path>
                </svg>
                большие буквы</span>
                <span :class="{'dt-verification-password-checkboxes__ready':hasNumber}">
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"
                         width="12px" height="12px" style="padding-bottom: -2px">
                      <path fill="currentColor"
                            d="M173.898 439.404l-166.4-166.4c-9.997-9.997-9.997-26.206 0-36.204l36.203-36.204c9.997-9.998 26.207-9.998 36.204 0L192 312.69 432.095 72.596c9.997-9.997 26.207-9.997 36.204 0l36.203 36.204c9.997 9.997 9.997 26.206 0 36.204l-294.4 294.401c-9.998 9.997-26.207 9.997-36.204-.001z">
                      </path>
                    </svg>
                    цифры</span>
            </div>
        </div>

        <password-input :name="'password_repeat'"
                        :data-vv-as="$t('password_repeat')"
                        v-validate="'confirmed:password|required'"
                        :value="password_repeat"
                        :placeholder="'Повторите пароль'"
                        :class="{'validate-error': errors.has('password_repeat') && showPasswordError}"
                        @change.native="showPasswordError = true"
                        v-model="password_repeat">

            <validation-error v-if="errors.has('password_repeat') && showPasswordError"
                              :message="errors.first('password_repeat')"></validation-error>
        </password-input>

        <Captcha></Captcha>

        <simple-checkbox class="dt-registration__firs-checkbox"
                         :name="'agree'"
                         :id="'label_agree'"
                         :fieldValue="'label_agree'"
                         v-validate="'required:true'"
                         :data-vv-as="$t('agree')"
                         :for="'label_agree'"
                         :class="{'validate-error': errors.has('agree')}"
                         :label="'ознакомлен (на) и согласен (на) с <a href=\'https://cashberry.com.ua/documents/crediting_rules.pdf\'>Правилами</a><span class=\'dt-notation-content\' title=\'Обязательное поле!\'>*</span>'"
                         v-model="agree">

            <validation-error v-if="errors.has('agree')"
                              :message="errors.first('agree')"></validation-error>
        </simple-checkbox>

        <simple-checkbox :name="'is_confirmed_kyivstar'"
                         :id="'is_confirmed_kyivstar'"
                         :fieldValue="'is_confirmed_kyivstar'"
                         v-validate="'required:true'"
                         :data-vv-as="$t('is_confirmed_kyivstar')"
                         :for="'is_confirmed_kyivstar'"
                         :class="{'validate-error': errors.has('is_confirmed_kyivstar')}"
                         :label="'ознакомлен (на) и согласен (на) с <a href=\'https://cashberry.com.ua/documents/Personal_data_processing.pdf\'>Порядком</a>, подтверждаю своё согласие на обработку своих персональных данных, в том числе на передачу данных в бюро кредитных историй, и согласие на обработку телекоммуникационных данных<span class=\'dt-notation-content\' title=\'Обязательное поле!\'>*</span>'"
                         v-model="is_confirmed_kyivstar">

            <validation-error v-if="errors.has('is_confirmed_kyivstar')"
                              :message="errors.first('is_confirmed_kyivstar')"></validation-error>
        </simple-checkbox>


        <simple-checkbox :name="'infoAgree'"
                         :id="'label_information'"
                         :for="'label_information'"
                         :fieldValue="'label_information'"
                         :label="'предоставляю и подтверждаю своё согласие на получение уведомлений, специальных предложений от Cashberry и его партнеров'"
                         v-model="infoAgree">
        </simple-checkbox>

        <div class="dt-button-block">
            <div @click="onSubmit" class="dt-button">Продолжить</div>
        </div>
    </div>
</template>

<script>
    import PhoneInput from '../Inputs/PhoneInput';
    import PasswordInput from '../Inputs/PasswordInput';
    import TextInput from '../Inputs/TextInput';
    import ValidationError from '../Inputs/ValidationError';
    import Captcha from '../Inputs/Captcha';
    import SimpleCheckbox from '../Inputs/CheckboxInput';

    export default {
        props: {
            phone: {
                type: String,
                required:true,
            },
        },
        data() {
            return {
                hasNumber: false,
                hasUpper: false,
                hasLower: false,
                hasSixChars: false,
                email: '',
                regPhone: this.phone,
                password: '',
                password_repeat: '',
                showPasswordError: false,
                isRegistrationConplete: false,
                agree: false,
                is_confirmed_kyivstar: false,
                infoAgree: false,
            };
        },
        components: {
            PhoneInput,
            ValidationError,
            TextInput,
            PasswordInput,
            Captcha,
            SimpleCheckbox,
        },
        methods: {
            changeInput() {
                if (/\d/.test(this.password)) {
                    this.hasNumber = true;
                } else {
                    this.hasNumber = false;
                }

                if (/[A-ZА-ЯЇЄІЁ]/.test(this.password)) {
                    this.hasUpper = true;
                } else {
                    this.hasUpper = false;
                }
                if (/[a-zа-яїєіё]/.test(this.password)) {
                    this.hasLower = true;
                } else {
                    this.hasLower = false;
                }
                if (this.password.length >= 6) {
                    this.hasSixChars = true;
                } else {
                    this.hasSixChars = false;
                }
            },
            async onSubmit() {
                const capcha = document.querySelector('.g-recaptcha-response').value;

                if (!capcha) {
                    const that = this;
                    document.getElementById('js-error_message_capcha').classList.add('validate-error');

                    setTimeout(() => {
                        that.removeErrorValid();
                    }, 3000);
                }

                this.showPasswordError = true;

                if (!await this.$validator.validateAll() || !capcha) {
                    this.scrollToError();
                    return;
                }

                alert('Hello from Dima!');
            },
            removeErrorValid() {
                document.getElementById('js-error_message_capcha').classList.remove('validate-error');
            },
            scrollToError() {
                this.$el.querySelector('.validate-error').scrollIntoView({
                    behavior: 'smooth',
                    block: 'center',
                });
            },

            // checkFullData() {
            //     const checkFields = this.regPhone.length === 19 && this.email.length > 4 && this.password.length > 5 &&
            //             this.password_repeat.length > 5 && this.agree && this.is_confirmed_kyivstar;
            //     const checkErrors = this.$el.querySelector('.validate-error');
            //     !checkFields || checkErrors !== null ? this.isRegistrationConplete = false : this.isRegistrationConplete = true;
            // },
            // repeatPasswordChangeEvent() {
            //     this.showPasswordError = true;
            //     this.checkFullData();
            // },


        },
        mounted() {
        },

    };
</script>